import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Slider from "react-rangeslider";
import { Button } from "react-bootstrap";

import NavBar from "../../../components/NavBar/NavBar";
import StartQuoteBtn from "../../../components/StartQuoteBtn/StartQuoteBtn";

import "./FhaCalculator.css";
import "react-rangeslider/lib/index.css";

const n = 1.75;
function FhaCalculator() {
  const { t } = useTranslation();
  const [purchasePrice, setPurchasePrice] = useState(200000);
  const [downPaymentPercent, setDownPaymentPercent] = useState(3.5);
  const [numTerm, setNumTerm] = useState(30);
  const [rate, setRate] = useState(5.0);
  const [propertyTax, setPropertyTax] = useState(1.2);
  const [homeOwnerInsurance, setHomeOwnerInsurance] = useState(0.35);
  const [credit, setCredit] = useState("good");
  const [downPayment, setDownPayment] = useState(7000);
  const [monthlyPayment, setMonthlyPayment] = useState(0);
  const [amountFinanced, setAmountFinanced] = useState(0);
  const [fundingFee, setFundingFee] = useState(0);
  const [taxes, setTaxes] = useState(5.25);
  const [insurance, setInsurance] = useState();
  const [fhaMip, setFhaMip] = useState(0);

  const roundToTwoDecimal = (value) => {
    return parseFloat(value).toFixed(2);
  };

  const handleChangePurchasePrice = (value) => {
    if (value !== null) {
      setPurchasePrice(roundToTwoDecimal(value));
      setDownPayment(roundToTwoDecimal((value * downPaymentPercent) / 100));
    }
  };

  const handleChangeDownPaymentPercent = (value) => {
    if (value !== null) {
      setDownPaymentPercent(roundToTwoDecimal(value));
      setDownPayment(roundToTwoDecimal((purchasePrice * value) / 100));
    }
  };

  const handleChangeNumTerm = (e) => {
    if (e !== null) {
      setNumTerm(parseInt(e.currentTarget.getAttribute("value")));

      let tempRate = 0;
      if (30 === parseInt(e.currentTarget.getAttribute("value"))) {
        switch (credit) {
          case "poor":
            tempRate = 5.5;
            break;
          case "fair":
            tempRate = 5.25;
            break;
          case "good":
            tempRate = 5;
            break;
          case "excellent":
            tempRate = 4.875;
            break;
          default:
            tempRate = 5;
            break;
        }
      } else {
        switch (credit) {
          case "poor":
            tempRate = 5.25;
            break;
          case "fair":
            tempRate = 5;
            break;
          case "good":
            tempRate = 4.875;
            break;
          case "excellent":
            tempRate = 4.5;
            break;
          default:
            tempRate = 4.875;
            break;
        }
      }

      setRate(tempRate);
    }
  };

  const handleChangeRate = (value) => {
    if (value !== null) {
      setRate(roundToTwoDecimal(value));
    }
  };

  const handleChangePropertyTax = (value) => {
    if (value !== null) {
      setPropertyTax(roundToTwoDecimal(value));
    }
  };

  const handleChangeHomeOwnerInsurance = (value) => {
    if (value !== null) {
      setHomeOwnerInsurance(roundToTwoDecimal(value));
    }
  };

  const handleChangeCredit = (e) => {
    if (e !== null) {
      setCredit(e.currentTarget.getAttribute("value"));

      let tempRate = 0;
      if (30 === numTerm) {
        switch (e.currentTarget.getAttribute("value")) {
          case "poor":
            tempRate = 5.5;
            break;
          case "fair":
            tempRate = 5.25;
            break;
          case "good":
            tempRate = 5;
            break;
          case "excellent":
            tempRate = 4.875;
            break;
          default:
            tempRate = 5;
            break;
        }
      } else {
        switch (e.currentTarget.getAttribute("value")) {
          case "poor":
            tempRate = 5.25;
            break;
          case "fair":
            tempRate = 5;
            break;
          case "good":
            tempRate = 4.875;
            break;
          case "excellent":
            tempRate = 4.5;
            break;
          default:
            tempRate = 4.875;
            break;
        }
      }

      setRate(tempRate);
    }
  };

  useEffect(() => {
    let tempFundingFee = Math.floor(((purchasePrice - downPayment) * n) / 100);
    setFundingFee(roundToTwoDecimal(tempFundingFee));

    const tempAmountFinanced = purchasePrice - downPayment + tempFundingFee;

    setAmountFinanced(roundToTwoDecimal(tempAmountFinanced));

    let h = rate / 100 / 12;
    let g = 12 * numTerm;
    let tempTaxes = (purchasePrice * propertyTax) / 100 / 12;
    let tempInsurance = (tempAmountFinanced * homeOwnerInsurance) / 100 / 12;
    let y = tempAmountFinanced;
    let L = downPaymentPercent;

    let x = 0;

    if (30 === numTerm) {
      if (y <= 625500) {
        if (L <= 5) {
          x = 0.85;
        } else {
          x = 0.8;
        }
      } else {
        if (L <= 5) {
          x = 1.05;
        } else {
          x = 1;
        }
      }
    } else {
      if (y <= 625500) {
        if (L <= 10) {
          x = 0.7;
        } else {
          x = 0.8;
        }
      } else {
        if (L >= 22) {
          x = 0.45;
        } else {
          if (L >= 10) {
            x = 0.7;
          } else {
            x = 0.9;
          }
        }
      }
    }

    let tempFhaMip = (tempAmountFinanced * x) / 100 / 12;

    setTaxes(roundToTwoDecimal(tempTaxes));
    setInsurance(roundToTwoDecimal(tempInsurance));
    setFhaMip(roundToTwoDecimal(tempFhaMip));

    const tempMonthlyPayment =
      (amountFinanced * Math.pow(1 + h, g) * h) / (Math.pow(1 + h, g) - 1) +
      tempTaxes +
      tempInsurance +
      tempFhaMip;

    setMonthlyPayment(roundToTwoDecimal(tempMonthlyPayment));
  }, [
    purchasePrice,
    downPaymentPercent,
    numTerm,
    rate,
    propertyTax,
    homeOwnerInsurance,
    credit,
    downPayment,
    monthlyPayment,
    amountFinanced,
    fundingFee,
    taxes,
    insurance,
    fhaMip,
  ]);

  return (
    <>
      <NavBar />
      <div className="container">
        <div className="row d-flex justify-content-center mt-5">
          <div className="col-sm-7 center tool-btn-div">
            <ul>
              <li className="tool-btn-active">
                <a href="/tools/fha-calculator">
                  <strong>{t("tools.home.btn_1")}</strong>
                </a>
              </li>
              <li>
                <a href="/tools/fha-affordability-calculator">
                  <strong>{t("tools.home.btn_2")}</strong>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div className="row d-flex justify-content-center mb-5">
          <div className="col-sm-7 center">
            <img
              className="h-img"
              src="/images/tools/t-1.png"
              alt="tools"
            ></img>
            <h3 className="header-text-2">{t("tools.fhaCalculator.head")}</h3>
            <p>{t("tools.fhaCalculator.txt_1")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7">
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaCalculator.txt_2")}</p>
                  </div>
                  <div className="col input-col">
                    <span>$</span>
                    <input
                      className="input-box"
                      disabled={true}
                      value={purchasePrice}
                    />
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={0}
                  max={721050}
                  step={1000}
                  value={purchasePrice}
                  onChange={handleChangePurchasePrice}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>$0</p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaCalculator.txt_3")}</p>
                  </div>
                  <div className="col input-col">
                    <span>(${downPayment})</span>
                    <input
                      className="input-box"
                      disabled={true}
                      value={downPaymentPercent}
                    />
                    <span>%</span>
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={3.5}
                  max={25}
                  step={0.1}
                  value={downPaymentPercent}
                  onChange={handleChangeDownPaymentPercent}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>3.5%</p>
                  </div>
                  <div className="col input-box-bottom-right">
                    <p>25%</p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaCalculator.txt_4")}</p>
                  </div>
                </div>
                <div className="row input-box-div">
                  <div className="col mb-3 mb-3">
                    <div
                      className={
                        numTerm === 15
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={15}
                      onClick={handleChangeNumTerm}
                    >
                      {t("tools.fhaCalculator.txt_32")}
                    </div>
                  </div>
                  <div className="col mb-3">
                    <div
                      className={
                        numTerm === 30
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={30}
                      onClick={handleChangeNumTerm}
                    >
                      {t("tools.fhaCalculator.txt_33")}
                    </div>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaCalculator.txt_5")}</p>
                  </div>
                  <div className="col input-col">
                    <input className="input-box" disabled={true} value={rate} />
                    <span>%</span>
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={3}
                  max={10}
                  step={0.005}
                  value={rate}
                  onChange={handleChangeRate}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>3%</p>
                  </div>
                  <div className="col input-box-bottom-right">
                    <p>10%</p>
                  </div>
                </div>
                <div className="row">
                  <div className="col center">
                    <p className="sub-input-text">
                      {t("tools.fhaCalculator.txt_6")}
                    </p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaCalculator.txt_7")}</p>
                  </div>
                  <div className="col input-col">
                    <input
                      className="input-box"
                      disabled={true}
                      value={propertyTax}
                    />
                    <span>%</span>
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={0.6}
                  max={2.5}
                  step={0.1}
                  value={propertyTax}
                  onChange={handleChangePropertyTax}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>0.6%</p>
                  </div>
                  <div className="col input-box-bottom-right">
                    <p>2.5%</p>
                  </div>
                </div>
                <div className="row">
                  <div className="col center">
                    <p className="sub-input-text">
                      {t("tools.fhaCalculator.txt_8")}
                    </p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaCalculator.txt_9")}</p>
                  </div>
                  <div className="col input-col">
                    <input
                      className="input-box"
                      disabled={true}
                      value={homeOwnerInsurance}
                    />
                    <span>%</span>
                  </div>
                </div>

                <Slider
                  className="slider-input"
                  min={0.2}
                  max={0.5}
                  step={0.05}
                  value={homeOwnerInsurance}
                  onChange={handleChangeHomeOwnerInsurance}
                  tooltip={false}
                />

                <div className="row">
                  <div className="col">
                    <p>0.2%</p>
                  </div>
                  <div className="col input-box-bottom-right">
                    <p>0.5%</p>
                  </div>
                </div>
                <div className="row">
                  <div className="col center">
                    <p className="sub-input-text">
                      {t("tools.fhaCalculator.txt_10")}
                    </p>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row d-flex justify-content-center">
              <div className="col-sm-10 mt-3">
                <div className="row input-box-div">
                  <div className="col">
                    <p>{t("tools.fhaCalculator.txt_11")}</p>
                  </div>
                </div>
                <div className="row input-box-div">
                  <div className="col mb-3 mb-3">
                    <div
                      className={
                        credit === "poor"
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={"poor"}
                      onClick={handleChangeCredit}
                    >
                      <p>{t("tools.fhaCalculator.txt_12")}</p>
                      <p>(620-639)</p>
                    </div>
                  </div>
                  <div className="col mb-3 mb-3">
                    <div
                      className={
                        credit === "fair"
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={"fair"}
                      onClick={handleChangeCredit}
                    >
                      <p>{t("tools.fhaCalculator.txt_13")}</p>
                      <p>(640-659)</p>
                    </div>
                  </div>
                  <div className="col mb-3 mb-3">
                    <div
                      className={
                        credit === "good"
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={"good"}
                      onClick={handleChangeCredit}
                    >
                      <p>{t("tools.fhaCalculator.txt_14")}</p>
                      <p>(660-719)</p>
                    </div>
                  </div>
                  <div className="col mb-3 mb-3">
                    <div
                      className={
                        credit === "excellent"
                          ? "checkbox-button checkbox-button-active"
                          : "checkbox-button"
                      }
                      value={"excellent"}
                      onClick={handleChangeCredit}
                    >
                      <p>{t("tools.fhaCalculator.txt_15")}</p>
                      <p>(720+)</p>
                    </div>
                  </div>
                </div>
                <hr className="hr-input" />
              </div>
            </div>
          </div>
          <div className="col-sm-4">
            <div className="row">
              <div className="col center">
                <hr className="hr-input" />
                <p>{t("tools.fhaCalculator.txt_21")}</p>
                <h1>
                  <b>${monthlyPayment}</b>
                </h1>
                <Button href="/start" variant="warning">
                  {t("quoteReq.btn")}
                </Button>
                <hr className="hr-input" />
              </div>
            </div>
            <div className="row">
              <div className="col center result-div">
                <p>
                  <b>{t("tools.fhaCalculator.txt_22")}</b>
                </p>
                <ul>
                  <li>
                    <strong>{t("tools.fhaCalculator.txt_23")}</strong>
                    <span class="result_purchase_price">${purchasePrice}</span>
                  </li>
                  <li>
                    <strong>{t("tools.fhaCalculator.txt_24")}</strong>
                    <span class="result_fundingfee">${fundingFee}</span>
                  </li>
                  <li>
                    <strong>{t("tools.fhaCalculator.txt_25")}</strong>
                    <span class="result_amount_financed">
                      ${amountFinanced}
                    </span>
                  </li>
                </ul>
              </div>
            </div>
            <div className="row">
              <div className="col">
                <p>
                  {t("tools.fhaCalculator.txt_26")}
                  <b>${fhaMip}</b>
                  {t("tools.fhaCalculator.txt_27")}
                </p>
                <p>
                  {t("tools.fhaCalculator.txt_28")}
                  {propertyTax}%{t("tools.fhaCalculator.txt_29")}
                  <b>${taxes}</b>
                  {t("tools.fhaCalculator.txt_30")}
                  <b>${insurance}</b>
                  {t("tools.fhaCalculator.txt_31")}
                  {homeOwnerInsurance}%.
                </p>
              </div>
            </div>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-10">
            <h3 className="header-text-2">{t("tools.fhaCalculator.txt_16")}</h3>
            <p>{t("tools.fhaCalculator.txt_17")}</p>
            <p>{t("tools.fhaCalculator.txt_18")}</p>
            <h3 className="header-text-2">{t("tools.fhaCalculator.txt_19")}</h3>
            <p>{t("tools.fhaCalculator.txt_20")}</p>
          </div>
        </div>
      </div>
      <StartQuoteBtn />
    </>
  );
}

export default FhaCalculator;
