import { Button } from "react-bootstrap";
import { useTranslation } from "react-i18next";

import "./StartQuoteBtn.css";

function StartQuoteBtn() {
  const { t } = useTranslation();
  return (
    <div className="check-req-div">
      <div className="container">
        <div className="row d-flex justify-content-center hero-text pt-5 pb-5">
          <div className="col-sm-8 center mt-5 mb-5">
            <hr className="global-hr-tag" />
            <h2 className="head-text-style mt-4">{t("quoteReq.head")}</h2>
            <p className="gray-text-style">{t("quoteReq.sub")}</p>
            <Button href="/start" variant="warning">
              {t("quoteReq.btn")}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default StartQuoteBtn;
