import React from "react";
import { Button, Container, Nav, Navbar, NavDropdown } from "react-bootstrap";
import { useTranslation } from "react-i18next";

import "./NavBar.css";

function NavBar({ navType = "light" }) {
  const { t, i18n } = useTranslation();

  const changeLanguage = () => {
    if (i18n.language === "en") {
      i18n.changeLanguage("es");
    } else {
      i18n.changeLanguage("en");
    }
  };

  return (
    <Navbar
      className={
        navType === "dark"
          ? "top-nav-bar top-nav-bar-dark"
          : "top-nav-bar top-nav-bar-light"
      }
      collapseOnSelect
      expand="lg"
      bg="transparent"
      variant={navType === "dark" ? "dark" : "light"}
    >
      <Container>
        <Navbar.Brand href="/">
          <img
            src={
              navType === "dark"
                ? "/images/logo.webp"
                : "/images/logo-dark.webp"
            }
            height="55"
            className="d-inline-block align-top"
            alt="React Bootstrap logo"
          />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto"></Nav>
          <Nav>
            <NavDropdown
              title={t("navBar.Loan_Program")}
              id="nav-dropdown-loan"
            >
              <NavDropdown.Item href="/fha-loan-guide">
                {t("navBar.FHA_Loans")}
              </NavDropdown.Item>
              <NavDropdown.Item href="/va-loans">
                {t("navBar.VA_Loans")}
              </NavDropdown.Item>
              <NavDropdown.Item href="/fannie-mae">
                {t("navBar.Fannie_Mae")}
              </NavDropdown.Item>
            </NavDropdown>
            <Nav.Link href="/tools">{t("navBar.Tools_&_Resources")}</Nav.Link>
            <NavDropdown title={t("navBar.More")} id="nav-dropdown-loan">
              <NavDropdown.Item href="/articles">
                {t("navBar.Articles")}
              </NavDropdown.Item>
              <NavDropdown.Item href="/videos">
                {t("navBar.Videos")}
              </NavDropdown.Item>
              <NavDropdown.Item href="/contact">
                {t("navBar.contact")}
              </NavDropdown.Item>
            </NavDropdown>
            <Button className="nav-btn-a" href="/start" variant="warning">
              {t("navBar.Check_My_Eligibility")}
            </Button>
            <Button
              className="language-btn"
              onClick={() => changeLanguage()}
              variant="default"
            >
              <img
                src={
                  i18n.language === "en"
                    ? "/images/country-flags/gb.svg"
                    : "/images/country-flags/es.svg"
                }
                alt="flag"
              ></img>
            </Button>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavBar;
